.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.background{
  z-index: -1;
  display: flex;
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
}

.guy{
  width: 35vw;
  height: 80vh;
  position: fixed;
  bottom: 4vh;
  right: 4vw;
}

.App-header {
  background-color: transparent;
  display: inline-block;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
  width: 100vw;
}

h1{
  width: 40vw;
  height: 10vh;
  font-size: xx-large;
}

.menu{
  display: inline-block;
  position: fixed;
  top: 5vh;
  right: 0;
  width: 60vw;
}

.menu p{
  position: relative;
  top: 0;
  right: 0;
  display: inline-block;
  border-radius: 30px;
  margin: 5px 10px;
  padding: 5px 20px;
}
.menu p:hover{
  background-color: black;
  color: white;
  border-radius: 30px;
  transition: 500ms;
  cursor: pointer;
}

.selected{
  background-color: black;
  color: white;
  border-radius: 30px;
  transition: 500ms;
  cursor: pointer;
}

table{
  position: fixed;
  left: 10vw;
  top: 25vh;
  width: 40vw;
  border: 1px solid black;
}

td{
  border: 2px solid black;
  width: 20vw;
  padding: 10px;
}
li{
  font-size: small;
}

.contact{
  position: fixed;
  bottom: 10px;
  left: 2vw;
  display: block;
  font-size: 1rem;
}